@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.NavMain {
  background-color: transparent;
  color: #fffaf0;
  left: 0;
  right: 0;
  position: -webkit-sticky;
  position: sticky;
  z-index: 2;
  align-items: center;
  padding: 20px;
  height: 70px;
}

.NavMain .nav-list {
  margin-left: auto;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.NavBottom {
  background-color: transparent;
  color: #fffaf0;
  position: fixed;
  left: 0;
  bottom: 0;
  align-items: center;
  padding: 20px;
  height: 70px;
}

.NavBottom .nav-list {
  margin-left: auto;
  display: flex;
  justify-content: space-around;
}

.NavBottom .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.missing {
  height: calc(100% - 60px);
  padding-bottom: 70px;
}

.missing_title {
  text-align: center;
}

.missing_info {
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.missing_info > img {
  text-align: center;
}


*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{animation-duration:.01ms !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Manrope", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

